}

h3 {
    font-size: 24px;
    color: #524743;
    text-align: left;
}

.building {
    margin-top: 140px;
    padding-bottom: 100px;
}

.gallery {
    line-height: 0;
    -webkit-column-count:3;
    -webkit-column-gap:8px;
    -moz-column-count:3;
    -moz-column-gap:8px;
    column-count:3;
    grid-column-gap:5px;
    column-gap:5px;
}

.gallery img {
    width: 100% !important;
    height: auto !important;
    margin-bottom:5px;
    transition:.2s;
}

@media (max-width:1400px) {
    .gallery {
        column-count:2;
    }
}

@media (max-width:1000px) {
    .gallery {
        column-count:1;
    }
}
.navigate {
    position:fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    overflow: hidden;
    align-items: center;
    background-color:blanchedalmond;
    z-index: 3;
}

